// Colours
$text: #1B1718;
$grey: #f1f1ee;
$lightgrey: #95A3A9;
$border: #F1F1EF;
$black: $text;
$bg: white;

$red: #cc6600;
$green: #003333;
$blue: #0F5178;

@mixin fullBrowser {
  height: calc(100vh - 24px);
  width: calc(100vw - 24px);
  @media screen and (min-width: 768px) {
    height: calc(100vh - 40px);
    width: calc(100vw - 40px);
  }
}

@mixin transit {
  transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1);
}

@mixin transitDelay {
  transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1) 0.3s;
}