@font-face {
    font-family: 'marrthinitalic';
    src: url('/assets/fonts/marrthinitalic.woff2') format('woff2'),
        url('/assets/fonts/marrthinitalic.woff') format('woff');
    font-weight: 300;
    font-style: italic;
    font-display: swap; /* Read next point */  
    unicode-range: U+000-5FF; /* Download only latin glyphs */  
}

@font-face {
    font-family: 'marritalic';
    src: url('/assets/fonts/marritalic.woff2') format('woff2'),
        url('/assets/fonts/marritalic.woff') format('woff');
    font-weight: normal;
    font-style: italic;
    font-display: swap; /* Read next point */  
    unicode-range: U+000-5FF; /* Download only latin glyphs */  
}

@font-face {
    font-family: 'Druk Cond Super';
    src: url('/assets/fonts/DrukCond-Super.woff2') format('woff2'),
        url('/assets/fonts/DrukCond-Super.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap; /* Read next point */  
    unicode-range: U+000-5FF; /* Download only latin glyphs */  
}


@mixin headfont {
    font-family: 'marritalic', serif;
    font-weight: normal;
    font-style: italic;
}

@mixin bodyfont {
    font-family: 'marrthinitalic', serif;
    font-weight: 300;
    font-style: italic;
}

body {
    @include bodyfont;
    font-weight: 300;
    font-weight: normal;
    font-size: 15px;
    line-height: 1.5;
    color: $text;
}
@mixin druk {
    font-family: 'Druk Cond Super', Baskerville, Baskerville Old Face, Hoefler Text, Garamond, Times New Roman, serif;
    font-weight: normal;
    font-style: normal;
}
.cc-window.cc-banner {
    padding: 5px 20px;
}
.cc-banner .cc-message, .cc-compliance {
    @include bodyfont;
    font-size: 13px;
    line-height: 1.5;
    color: $text;
    .cc-btn {
        display: block;
        padding: 4px 4px;
        font-size: 13px;
        font-weight: 300;
        border-width: 1px;
        border-style: solid;
        text-align: center;
        white-space: nowrap;
        line-height: 22px;
        min-width: 135px;
        transition: all 0.3s ease;
    }
}
a {
    text-decoration: none;
    transition: all 0.3s ease;
    color: $text;
    .rte & {
        color: $blue;
    }
    // .white & {
    //     @media screen and (min-width: 800px) {
    //         color: white;
    //     }
    // }
}
cite {
    text-transform: uppercase;
    font-style: normal;
    letter-spacing: 1.05px;
}
blockquote {
    font-style: italic;
    text-align: center;
    padding: 16px 16px 8px 16px;
    margin: 0;
    @media screen and (min-width: 960px) {
        padding: 32px 32px 8px 32px;
    }
    + cite {
        text-align: center;
        display: block;
    }
}

