h1.drukster, strong.drukster {
  @include druk;
  color: white;
  font-size: 256px;
  font-size: 28vw;
  line-height: 0.8;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  z-index: 20;
  @media screen and (min-width: 768px) {
    font-size: 23vw;
  }
  @media screen and (min-width: 1260px) {
    font-size: 23vw;
  }
  &.outline {
      color: white; /* Unfortunately you can't use transparent here  */
      text-shadow:
       -1px -1px 0 #000,  
      1px -1px 0 #000,
      -1px 1px 0 #000,
       1px 1px 0 #000
  }
  &.page {
    color: black;
    margin: 20px auto 40px 0;
    text-align: left;
    max-width: 100%;
  }
  /* Real outline for modern browsers */
  @supports((text-stroke: 1px black) or (-webkit-text-stroke: 1px black)) {
    &.outline {
      color: transparent;
      -webkit-text-stroke: 1px black;
      text-stroke: 1px black;
      text-shadow: none;
    }
  }
}
h1.drukster, .scroll_text strong.drukster {
  max-width: 94%;
  text-align: center;
  margin: 0 auto;
  @media screen and (min-width: 768px) {
    max-width: 90%;
  }
  @media screen and (min-width: 1260px) {
    max-width: 80%;
  }
}
