// messageing
.error, .pristine-error {
    color: $red;
}
.success {
    color: $green;
}

// Lyrics sections use fittext and outline class
.lyrics {
  position: relative;
  z-index: 23;
  padding: 32px 16px 0;
  @media screen and (min-width: 768px) {
    padding: 64px 32px 0 32px;
  }
  @media screen and (min-width: 1024px) {
    padding: 48px 32px 0 32px;
  }
  .lyric {
    width: 100%;
    strong {
      width: auto;
    }
  }
}