.flex.three {
  position: relative;
  z-index:1;
  .image, .story, .col, .empty {
    flex: 1;
    flex-basis: 100%;
    max-width: 100%;
    .druk {
      font-size: 25vw;
    }
    @media screen and (min-width: 768px) {
      flex-basis: 33.333333%;
      max-width: 33.333333%;
      .druk {
        font-size: 6.5vw;
      }
    }
  }
}
.flex.two {
  .image, .story, .empty {
    flex: 1;
    flex-basis: 100%;
    max-width: 100%;
    .druk {
      font-size: 25vw;
    }
    @media screen and (min-width: 768px) {
      flex-basis: 50%;
      max-width: 50%;
      .druk {
        font-size: 13vw;
      }
    }
  }
  &.bigger-image {
    .image.bigger {
      flex: 2;
      flex-basis: 100%;
      max-width: 100%;
      @media screen and (min-width: 768px) {
        flex-basis: 66.66666%;
        max-width: 66.66666%;
      }
    }
    .story, .empty {
      flex: 2;
      flex-basis: 100%;
      max-width: 100%;
      @media screen and (min-width: 768px) {
        flex-basis: 33.33333%;
        max-width: 33.33333%;
        .druk {
          font-size: 6.5vw;
        }
      }
    }
  }
}

.flex.one {
  .image, .story, .blank, .video, .music, .button_container {
    flex: 1;
    flex-basis: 100%;
    max-width: 100%;
    .druk {
      font-size: 25vw;
    }
  }
  .story {
    @media screen and (min-width: 768px) {
      .druk {
        font-size: 16vw; 
      }
    }
  }
  .alignment {
    flex: 1 1 100%;
    &.left {
      justify-content: flex-start;
    }
    &.right {
      justify-content: flex-end;
    }
    &.centre {
      justify-content: center;
    }
  }
  .video {
    @media screen and (min-width: 768px) {
      &.twoThirds {
        max-width: 66.6666%;
      }
      &.half {
        max-width: 50%;
      }
      &.oneThird {
        max-width: 33.3333%;
      }
    }
    .videowrapper + p {
      margin-top: 12px;
    }
  }
  .button_container {
    padding: 20px 0;
    text-align: center;
    a {
      padding: 6px 12px 2px;
    }
  }
  .story, .empty {
    flex: 2;
    flex-basis: 100%;
    max-width: 100%;
  }
  .music {
    .musicwrapper {
      position: relative;
      iframe {
        position: absolute;
        right: 10%;
        bottom: calc(50% - 40px);
        z-index: 2;
      }
      .bgimage {
        position: relative;
        z-index: 1;
      }
    }
  }
  .videofile {
    width: 100%;
    flex: 1 1 100%;
    overflow: hidden;
    video {
      width: 100%;
      height: auto;
    }
    .button_container {
      position: absolute;
      top: 50%;
      bottom: 0;
      right: 0;
      left: 0;
      &.white {
        a {
          border-color: white;
          color: white;
          &:hover {
            color: $text;
          }
        }
      }
    }
    &.norespect {
      @media screen and (max-width: 800px) {
        video {
          width: auto;
          height: 100%;
          min-height: 80vh;
          margin-left: 60%;
          transform: translateX(-50%);
        }
      }
      @media screen and (max-width: 460px) {
        min-height: 90vh;
      }
    }
  }
}

.campaign {
  article {
    &:first-child {
      .videofile {
        margin-left: -10px;
        margin-right: -10px;
        @media screen and (min-width: 768px) {
          margin-left: -20px;
          margin-right: -20px;
        }
      }
    }
  }
}

