.campaigns, .collection_campaigns {
  // we need some mad indexing fro the animation
  .campaign, .product {
    position: relative;
    &:nth-child(1) {
      z-index: 21;
    }
    &:nth-child(2) {
      z-index: 20;
    }
    &:nth-child(3) {
      z-index: 19;
    }
    &:nth-child(4) {
      z-index: 18;
    }
    &:nth-child(5) {
      z-index: 17;
    }
    &:nth-child(6) {
      z-index: 16;
    }
    &:nth-child(7) {
      z-index: 15;
    }
    &:nth-child(8) {
      z-index: 14;
    }
    &:nth-child(9) {
      z-index: 13;
    }
    &:nth-child(10) {
      z-index: 12;
    }
    &:nth-child(11) {
      z-index: 11;
    }
    &:nth-child(12) {
      z-index: 10;
    }
    &:nth-child(13) {
      z-index: 9;
    }
    &:nth-child(14) {
      z-index: 8;
    }
    &:nth-child(15) {
      z-index: 7;
    }
    &:nth-child(16) {
      z-index: 6;
    }
    &:nth-child(17) {
      z-index: 5;
    }
    &:nth-child(18) {
      z-index: 4;
    }
    &:nth-child(19) {
      z-index: 3;
    }
    &:nth-child(20) {
      z-index: 2;
    }
    &:nth-child(21) {
      z-index: 1;
    }
  }
  .story {
    z-index: 2;
  }
  .image {
    z-index: 1;
    position: relative;
    .main_picture {
      position: relative;
      .picture_inner {
        position: relative;
        overflow: hidden;
      }
      .picture_mask {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        right: 0;
        bottom: 100%;
        background-color: white;
        z-index: 30;
      }
      .image_overlay {
        position: absolute;
        inset: 0;
        background-color: black;
      }
    }
    .card_content {
      position: absolute;
      inset: 0;
      padding: 20px;
      align-content: center;
      > * {
        flex: 1 0 100%;
        color: white;
        text-align: center;
      }
      h3 {
        @include druk();
        font-size: 750%;
        text-transform: uppercase;
        text-shadow: 0px 0px 10px #00000019;
        line-height: 1;
        margin-bottom: 0;
        transition: all 0.2s ease-in-out;
        @media screen and (min-width: 768px) {
          font-size: 530%;
        }
        @media screen and (min-width: 960px) {
          font-size: 750%;
        }
      }
      p {
        max-width: 410px;
        margin: 0 auto;
        &+div {
          margin-top: 12%;
        }
      }
      span.button {
        color: white;
        &:hover {
          background-color: black;
          border-color: black;
        }
      }
      &:hover h3 {
        @supports((text-stroke: 1px white) or (-webkit-text-stroke: 1px white)) {
            color: transparent;
            -webkit-text-stroke: 1px white;
            text-stroke: 1px white;
            text-shadow: none;
        }
      }
    }
    .flipper_outer {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: -40px;
      overflow: hidden;
    }
    .flipper-container {
      position: absolute;
      z-index: 2;
      top: 0;
      right: 0;
      bottom: 0;
      left: 40px;
      // overflow-y: hidden;
    }
    .flipper_inner {
      position: absolute;
      bottom: 100%;
      width: 100%;
      height: 50%;
      overflow: hidden;
      
      .mask {
        position: absolute;
        width: 102%;
        height: 102%;
        left: -1%;
        right: -1%;
        bottom: 100%;
        color: white;
        transform-origin: left bottom;
        box-shadow: 0 0 5px rgba(255,255,255,1);
        &:after {
          content: " ";
          height: 6px;
          top: 103%;
          left: 0;
          right: 0;
          background-color: white;
          border-radius: 3px;
          display: block;
          z-index: 7;
          position: absolute;
          filter: blur(3px);
          transition: opacity 1.2s ease-in;
        }
      }
    }
    .flipper {
      position: absolute; 
      display: block;
      right: 0;
      // width: 95.5%;
      width:100%;
      // max-width: 95.5%;
      max-width: 100%;
      z-index: 2;
      transform: scaleY(-1) rotate(0);
      transform-origin: top left;
      transform-origin: left 25%;
      opacity: 1;
      transition: all 1s ease-in;
      // box-shadow: 0 2px 4px red;
    }
    @media screen and (prefers-reduced-motion: no-preference) {
      &:nth-child(1) {
        .ohmygaad & {
          transform-origin: center center;
          animation: droppa 10.0s ease-in both 20s;
          .main_picture {
            transform-origin: 100% 53%;
            animation: swingdroppb 1.5s ease-in-out both 10.8s;
          }
          .picture_mask {
            animation: topmaskerb 5s linear both 8s;
          }
          .picture_outer {
            animation: fade-out 0.1s linear both 10.5s;
          }
          .flipper_outer {
            right: -40px;
            left: 0;

          }
          .flipper-container {
            left: 0;
            right: 40px;
          }
          .flipper_inner {
            transform-origin: right bottom;
            animation: flipperb 2.5s linear both 8s;
            .mask {
              animation: maskerb 2.5s linear both 8s;
              left: auto;
              right: 0;
              transform-origin: right bottom;
              &:after {
                animation: fadeinner 2.5s linear both 8s;
              }
            }
            .flipper {
              img {
                // animation: imagescale 2.0s linear both 5s;
              }
            }
          }
        }
        .settle & {
          animation: fade-in 0.65s ease-out both;
        }
      }
      &:nth-child(2) {
        .ohmygaad & {
          transform-origin: center center;
          animation: droppa 9.0s ease-in both 8.2s;
          .main_picture {
            transform-origin: 100% 53%;
            animation: swingdroppb 1.5s ease-in-out both 7.8s;
          }
          .picture_mask {
            animation: topmaskerb 5s linear both 5s;
          }
          .picture_outer {
            animation: fade-out 0.1s linear both 7.5s;
          }
          .flipper_outer {
            right: -40px;
            left: 0;

          }
          .flipper-container {
            left: 0;
            right: 40px;
          }
          .flipper_inner {
            transform-origin: right bottom;
            animation: flipperb 2.5s linear both 5s;
            .mask {
              animation: maskerb 2.5s linear both 5s;
              left: auto;
              right: 0;
              transform-origin: right bottom;
              &:after {
                animation: fadeinner 2.5s linear both 5s;
              }
            }
            .flipper {
              img {
                // animation: imagescale 2.0s linear both 5s;
              }
            }
          }
        }
        .settle & {
          animation: fade-in 0.65s ease-out both;
        }
      }
      &:nth-child(3) {
        .ohmygaad & {
          transform-origin: center center;
          animation: droppa 11s ease-in both 6.3s;
          .main_picture {
            transform-origin: 0% 53%;
            animation: swingdroppa 1.5s ease-in-out both 4.8s;
          }
          .picture_mask {
            animation: topmaskera 5s linear both 2s;
          }
          .picture_outer {
            animation: fade-out 0.1s linear both 4.5s;
          }
          .flipper_inner {
            transform-origin: left bottom;
            animation: flippera 2.5s linear both 2s;
            .mask {
              animation: maskera 2.5s linear both 2s;
              &:after {
                animation: fadeinner 2.5s linear both 2s;
              }
            }
            .flipper {
              img {
                // animation: imagescale 2.5s linear both 2s;
              }
            }
          }
        }
        .settle & {
          animation: fade-in 0.65s ease-out both;
        }
      }
    }
    .mask-outer {
      position: absolute;
      z-index: 2;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      overflow: hidden;
    }
  
    .mask {
      position: absolute;
      display: block;
      width: 140%;
      left: 0;
      bottom: 100%;
      background-color: white;
      height: 50%;
      z-index: 6;
      // transform: rotate(10deg);
      transition: opacity 1s ease-in;
 
    }
  }
}

