// 1220 x 720 so 61 / 36
.scrollytext {
  margin-top: 16px;
  position: relative;
  scroll-snap-align: center;
  // overflow-y: scroll;
  // &:before {
  //   display: block;
  //   content: "";
  //   width: 100%;
  //   padding-top: (36 / 61) * 100%;
  // }
  .scroll_text {
    position: absolute;
    inset: 0;
    overflow-y: scroll;
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none; /* for Firefox */
    overflow-y: scroll; 
    &::-webkit-scrollbar {
        display: none; /* for Chrome, Safari, and Opera */
    }
  }
  .scroll_text {
    z-index: 3;
    padding-top: 16%;
    padding-bottom: 16%;
    a {
      display: block;
      text-align: center;
      margin: 0;
      &:first-child {
        margin-top: 36%;
        @media screen and (min-width: 460px) {
          margin-top: 20%;
        }
        @media screen and (min-width: 800px) {
          margin-top: 0;
        }
      }
      &:last-child {
        margin-bottom: 51%;
        @media screen and (min-width: 460px) {
          margin-bottom: 45%;
        }
        @media screen and (min-width: 800px) {
          margin-bottom: 3%;
        }
        // @media screen and (min-width: 1260px) {
        //   margin-bottom: 0;
        // }
      }
      strong {
        display: block;
        max-width: 100%;
        @include transit();
        word-break: break-word;
        font-size: 25vw;
        @media screen and (min-width: 768px) {
          font-size: 23vw;
        }
        @media screen and (min-width: 1260px) {
          font-size: 23vw;
          max-width: 82%;
        }
        @media screen and (min-width: 1400px) {
          max-width: 74%;
        }
      }
      &.outline {
        strong {
          color: white; /* Unfortunately you can't use transparent here  */
          text-shadow:
           -1px -1px 0 #fff,  
          1px -1px 0 #fff,
          -1px 1px 0 #fff,
           1px 1px 0 #fff
        }
        @supports((text-stroke: 1px white) or (-webkit-text-stroke: 1px white)) {
          strong {
              color: transparent;
              -webkit-text-stroke: 1px white;
              text-stroke: 1px white;
              text-shadow: none;
          }
        }
      }
    }
  }
  .fixed_image {
    z-index: 1;
    overflow: hidden;
    &:before {
      display: block;
      content: "";
      width: 100%;
      padding-top: 155.492958%;
        
      @media screen and (min-width: 480px) {
        padding-top: 135.576923%;
      }
      @media screen and (min-width: 800px) {
        padding-top: 59.0163934426%;
      }
    }
    .bgimage {
      overflow: hidden;
      position: absolute;
      inset: 0;
      img {
        position: absolute;
        inset: 0;
        object-fit: cover;
        width: 100%;
        height: auto;
        @include transit();
        opacity: 0;
      }
      &.intersecting {
        img {
          opacity: 1;
        }
      }
    }
  }
  
}